body {
  font-family: 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'WenQuanYi Micro Hei', sans-serif;
  word-wrap: break-word;
  line-height: 1.15;
  color: rgba(0, 0, 0, 0.85);
}
a {
  color: inherit;
  text-decoration: none;
}
ul,
p {
  margin: 0;
}
* {
  -webkit-overflow-scrolling: touch;
}
#root {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-size: 18px;
  overflow: hidden;
}
.fill {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.column {
  display: flex;
  align-items: stretch;
  flex-direction: column;
}
.box {
  display: flex;
  align-items: stretch;
  flex-direction: column;
}
.box > :not(.box-fill) {
  flex-shrink: 0;
}
.box > .box-fill {
  display: flex;
  align-items: stretch;
  flex-direction: column;
  flex: 1 1;
  position: relative;
}
.page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: stretch;
  flex-direction: column;
}
.page > :not(.box-fill) {
  flex-shrink: 0;
}
.page > .box-fill {
  display: flex;
  align-items: stretch;
  flex-direction: column;
  flex: 1 1;
  position: relative;
}
.transition {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.overflow {
  overflow: auto;
}
.pointer {
  cursor: pointer;
}
.primary,
.edit {
  color: #1890ff;
}
.success {
  color: #52c41a;
}
.warning {
  color: #faad14;
}
.error,
.delete {
  color: #f5222d;
}
.secondary {
  color: rgba(0, 0, 0, 0.45);
}
.disabled {
  color: rgba(0, 0, 0, 0.25);
}
